<template>
    <el-card>
        <template #header>
            <div class="cardHeader">
                <span>{{ $t('annotation.geometry.title') }}</span>
                <el-button id="seniorBtn" type="text">{{ $t('annotation.geometry.advanced') }}</el-button>
            </div>
        </template>
        <el-form label-position="left" label-width="auto" size="small" style="min-width: 300px;">
            <el-form-item :label="$t('annotation.geometry.block_size')">
                <div style="display: flex;">
                    <el-select>
                    <el-option label="256x256x256"></el-option>
                    </el-select>
                    <el-input type="number" v-model="width"></el-input>
                    <el-input type="number" v-model="height"></el-input>
                </div>
            </el-form-item>
            <el-form-item :label="$t('annotation.geometry.center')">
                <div style="display: flex;">
                    <el-input type="number" v-model="store.state.core.center.x"></el-input>
                    <el-input type="number" v-model="store.state.core.center.y"></el-input>
                    <el-input type="number" v-model="store.state.core.center.z"></el-input>
                </div>
            </el-form-item>
        </el-form>
    </el-card>
</template>

<script>
import { computed } from 'vue';
import { useStore } from 'vuex';

export default {
    name: 'geometryPanel',
    props: {
    },
    setup() {
        let store = useStore();
        let width = computed({
            get: () => store.state.core.blockSize.width,
        });
        let height = computed({
            get: () => store.state.core.blockSize.height
        });

        return {
            store,
            width,
            height
        }
    }
}
</script>

<style scoped>
#seniorBtn {
    border: 0px;
    padding: 0px;
}
</style>